import {createProfileTemplate} from './components/profile';
import {createSiteMenuTemplate} from './components/site-menu';
import {createFilmsSectionTemplate} from './components/films-section';
import {createFilmCardTemplate} from './components/film-card';
import {createShowMoreButtonTemplate} from './components/show-more-button';
// import {createFilmDetailsTemplate} from './components/film-details';

const ALL_FILMS = 5;
const EXTRA_FILMS = 2;

const render = (container, template, position) => {
  container.insertAdjacentHTML(position, template);
};

const mainElement = document.querySelector(`.main`);
const headerElement = document.querySelector(`.header`);

render(headerElement, createProfileTemplate(), `beforeend`);
render(mainElement, createSiteMenuTemplate(), `beforeend`);
render(mainElement, createFilmsSectionTemplate(), `beforeend`);

const allFilmsListElement = mainElement.querySelector(`.films-list`);
const allFilmsListContainer = allFilmsListElement.querySelector(`.films-list__container`);
for (let i = 0; i < ALL_FILMS; i++) {
  render(allFilmsListContainer, createFilmCardTemplate(), `beforeend`);
}
render(allFilmsListContainer, createShowMoreButtonTemplate(), `beforeend`);

const extraFilmsListElements = mainElement.querySelectorAll(`.films-list--extra`);
Array.from(extraFilmsListElements).forEach((section) => {
  const extraFilmsListContainer = section.querySelector(`.films-list__container`);
  for (let i = 0; i < EXTRA_FILMS; i++) {
    render(extraFilmsListContainer, createFilmCardTemplate(), `beforeend`);
  }
});

